.auth__input {
  width: 100%;
  padding: 0;
  padding-bottom: 13px;
  margin-bottom: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 2px solid #cccccc;
  font-weight: 500;
  font-size: 14px;
  line-height: 1.2;
  color: #ffffff;
}

.auth__input::placeholder {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.2;
  color: #cccccc;
}

.auth__input:last-of-type {
  margin-bottom: 0;
}

.auth__input:focus {
  outline: none;
}
